@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "PT Mono", "Consolas", monospace;
  color: #041827;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  scroll-behavior: smooth;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f8fdff;
}

a {
  color: #041827;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #041827;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1322px;
  margin: 0 auto;
  padding: 0 50px;
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  padding: 40px;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(4, 20, 39, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}

.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.modal__content {
  position: relative;
  max-width: 500px;
  padding: 40px;
  border: none;
  border-radius: 3px;
  background-color: #00b3ff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}

.modal__close-button {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}

.modal__title {
  margin-top: 0;
  margin-bottom: 15px;
  font-family: "Roboto Condensed", "Verdana", sans-serif;
  font-size: 30px;
  font-weight: 400;
  line-height: 42px;
  color: #ffffff;
}

.modal__text {
  margin-top: 0;
  margin-bottom: 40px;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
}

.modal__input {
  width: 100%;
  padding: 15px;
  margin-bottom: 20px;
  border: none;
  border-radius: 3px;
  line-height: 22px;
  background-color: rgba(4, 20, 39, 0.15);
  color: #ffffff;
}
.modal__input--text {
  padding-top: 25px;
}
.modal__input:last-child {
  margin-bottom: 25px;
}

.modal__button {
  display: block;
  width: 100%;
  margin-bottom: 20px;
}

.modal__checkbox-label {
  position: relative;
  padding-left: 25px;
  line-height: 16px;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
}
.modal__checkbox-label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 45%;
  transform: translateY(-50%);
  width: 15px;
  height: 15px;
  border: 1px solid rgba(255, 255, 255, 0.7);
  border-radius: 2px;
}

.modal__checkbox-input[type=checkbox]:checked + .modal__checkbox-label::before {
  border-color: #ffffff;
  background-image: url("data:image/svg+xml,%3Csvg width='9' height='6' viewBox='0 0 9 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.988525 2.33333L3.7778 5L7.96171 1' stroke='white'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-position: center;
}

.button {
  display: inline-flex;
  justify-content: center;
  word-wrap: break-word;
  padding: 10px 20px;
  font-family: "Roboto Condensed", "Verdana", sans-serif;
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
  background-color: rgba(255, 255, 255, 0);
  border: none;
  border-radius: 3px;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.button--dark {
  background-color: #041427;
}
.button--bright {
  background-color: #00b2ff;
}
.button--transparent-light {
  border: 1px solid #f2f5f6;
  color: #f2f5f6;
}
.button--transparent-bright {
  border: 1px solid #00b2ff;
  color: #00b2ff;
}
.button:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .button:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.button.focus-visible:focus {
  opacity: 0.8;
}

.header {
  position: sticky;
  width: 100%;
  margin-bottom: -17px;
  z-index: 5;
  background-image: linear-gradient(to bottom, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 30px;
  padding-bottom: 35px;
}
@media (max-width: 1023px) {
  .header__container {
    padding-top: 20px;
    padding-bottom: 25px;
  }
}
@media (max-width: 767px) {
  .header__container {
    padding-top: 10px;
    padding-bottom: 20px;
  }
}

.header__logo {
  align-self: flex-start;
  padding-right: 15px;
  flex-shrink: 0;
}
@media (max-width: 767px) {
  .header__logo {
    align-self: center;
  }
}

@media (max-width: 767px) {
  .header__logo img {
    width: 35px;
    height: 35px;
  }
}

.header__contacts {
  align-self: normal;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;
  row-gap: 20px;
}
@media (max-width: 1023px) {
  .header__contacts {
    column-gap: 20px;
  }
}

.header__contact {
  display: flex;
  align-items: center;
  color: #f2f5f6;
}
@media (max-width: 767px) {
  .header__contact:last-child {
    margin-right: 10px;
  }
}
@media (max-width: 1023px) {
  .header__contact {
    min-width: fit-content;
    padding: 20px;
  }
}
@media (max-width: 767px) {
  .header__contact {
    margin-right: 0;
    padding: 10px;
  }
}

.header__contact svg {
  margin-right: 15px;
}
@media (max-width: 1023px) {
  .header__contact svg {
    margin-right: 0;
  }
}

@media (max-width: 1023px) {
  .header__contact span {
    display: none;
  }
}

.header__button {
  max-width: 220px;
}
@media (max-width: 1023px) {
  .header__button {
    display: none;
  }
}

.promo {
  padding-top: 305px;
  padding-bottom: 280px;
  background-color: #041827;
  background-image: linear-gradient(to bottom, rgba(25, 32, 36, 0.7) 72%, rgba(25, 32, 36, 0) 100%), url("../img/bg-promo.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  color: #f2f5f6;
}
@media (min-resolution: 2dppx) {
  .promo {
    background-image: linear-gradient(to bottom, rgba(25, 32, 36, 0.7) 72%, rgba(25, 32, 36, 0) 100%), url("../img/bg-promo@2x.jpg");
  }
}
@media (max-width: 1023px) {
  .promo {
    padding-top: 180px;
    padding-bottom: 205px;
  }
}

.promo h1 {
  margin: 0;
  margin-bottom: 40px;
  font-family: "Roboto Condensed", "Verdana", sans-serif;
  font-size: 80px;
  font-weight: 700;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  word-wrap: break-word;
}
@media (max-width: 1023px) {
  .promo h1 {
    margin-bottom: 20px;
    font-size: 64px;
    line-height: 75px;
  }
}
@media (max-width: 767px) {
  .promo h1 {
    font-size: 36px;
    line-height: 42px;
  }
}

.promo p {
  margin: 0;
  margin-bottom: 40px;
  font-size: 22px;
  line-height: 34px;
  word-wrap: break-word;
}
@media (max-width: 1023px) {
  .promo p {
    margin-bottom: 50px;
  }
}
@media (max-width: 767px) {
  .promo p {
    font-size: 14px;
    line-height: 22px;
  }
}

.promo a {
  line-height: 21px;
  scroll-behavior: smooth;
}
.promo a:not(last-child) {
  margin-right: 20px;
  margin-bottom: 20px;
}

.promo a .short {
  display: none;
}
@media (max-width: 767px) {
  .promo a .short {
    display: block;
  }
}

@media (max-width: 767px) {
  .promo a .long {
    display: none;
  }
}

.benefits {
  padding: 120px 0;
}
@media (max-width: 1023px) {
  .benefits {
    padding: 80px 0;
  }
}
@media (max-width: 767px) {
  .benefits {
    padding: 50px 0;
  }
}

.benefits ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 31px;
  row-gap: 30px;
}
@media (max-width: 767px) {
  .benefits ul {
    grid-template-columns: 1fr;
  }
}

.benefits li {
  position: relative;
  min-height: 300px;
  padding: 40px 45px 35px;
  border: 1px solid #e5e5e5;
}
.benefits li::before {
  content: "";
  position: absolute;
  left: -1px;
  top: 0;
  width: 5px;
  height: 100%;
  background-color: #00b2ff;
}
@media (max-width: 1023px) {
  .benefits li {
    min-height: 360px;
    padding-left: 40px;
    padding-right: 35px;
  }
}
@media (max-width: 767px) {
  .benefits li {
    min-height: 340px;
    padding-left: 25px;
    padding-right: 30px;
  }
}
@media (hover: hover) {
  .benefits li:hover:not(.focus-visible) {
    color: #f2f5f6;
    background-color: #00b2ff;
  }
  .benefits li:hover:not(.focus-visible) svg,
.benefits li:hover:not(.focus-visible) h3 {
    color: #f2f5f6;
  }
}
.benefits li.focus-visible:focus {
  color: #f2f5f6;
  background-color: #00b2ff;
}
.benefits li.focus-visible:focus svg,
.benefits li.focus-visible:focus h3 {
  color: #f2f5f6;
}

.benefits svg {
  margin-bottom: 20px;
  color: #00b2ff;
}

.benefits h3 {
  margin-top: 0;
  margin-bottom: 15px;
  font-family: "Roboto Condensed", "Verdana", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 28px;
  color: #00b2ff;
}
@media (max-width: 767px) {
  .benefits h3 {
    font-size: 20px;
  }
}

.benefits p {
  margin: 0;
}
@media (max-width: 767px) {
  .benefits p {
    font-size: 14px;
  }
}

.about {
  padding-bottom: 100px;
}
@media (max-width: 1023px) {
  .about {
    padding-bottom: 80px;
  }
}

.about > div {
  display: flex;
  align-items: flex-start;
}

.about span {
  background: url("/img/about-bg.jpg");
  font-family: "Roboto Condensed", "Verdana", sans-serif;
  font-size: 156px;
  font-weight: 700;
  line-height: 220px;
  transform: rotate(180deg);
  writing-mode: vertical-rl;
  background-clip: text;
  color: transparent;
}
@media (max-width: 767px) {
  .about span {
    display: none;
  }
}

.about h2 {
  margin-top: 0;
  margin-bottom: 15px;
  font-family: "Roboto Condensed", "Verdana", sans-serif;
  font-size: 36px;
  line-height: 51px;
  font-weight: 400;
}
@media (max-width: 767px) {
  .about h2 {
    font-size: 28px;
  }
}

.about p {
  margin: 0;
  margin-bottom: 15px;
}
@media (max-width: 767px) {
  .about p {
    font-size: 14px;
  }
}

.about button {
  min-width: 180px;
}

p [data-id=to-hide],
[data-state=is-hidden] {
  display: none;
}

.products {
  padding: 70px 0 140px;
  background-color: #041827;
}
@media (max-width: 1023px) {
  .products {
    padding: 60px 0 75px;
  }
}
@media (max-width: 767px) {
  .products {
    padding: 50px 0 86px;
  }
}

.products h2 {
  margin: 0;
  margin-bottom: 50px;
  font-family: "Roboto Condensed", "Verdana", sans-serif;
  font-size: 36px;
  line-height: 51px;
  font-weight: 400;
  color: #ffffff;
}
@media (max-width: 767px) {
  .products h2 {
    font-size: 28px;
  }
}

.products h2 span:nth-child(2) {
  display: none;
}
@media (max-width: 767px) {
  .products h2 span:nth-child(2) {
    display: block;
  }
}

@media (max-width: 767px) {
  .products h2 span:nth-child(1) {
    display: none;
  }
}

.products ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, minmax(250px, 350px));
  grid-auto-rows: 650px;
  gap: 85px;
}
@media (max-width: 1023px) {
  .products ul {
    grid-template-columns: 1fr;
    grid-auto-rows: 250px;
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .products ul {
    grid-template-columns: auto;
  }
}

.products li {
  position: relative;
  background-color: rgba(255, 255, 255, 0.7);
  background-image: linear-gradient(to bottom, rgba(4, 24, 39, 0.5)), url("../img/bg-products.jpg");
  background-size: cover;
}
@media (min-resolution: 2dppx) {
  .products li {
    background-image: linear-gradient(to bottom, rgba(4, 24, 39, 0.5)), url("../img/bg-products@2x.jpg");
  }
}
.products li::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 60px;
  height: 60px;
  background-color: #00b2ff;
  background-image: url("../img/svg/icon-next.svg");
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 3px 0 0;
  cursor: pointer;
}
@media (max-width: 767px) {
  .products li::after {
    width: 30px;
    height: 30px;
  }
}

.products a {
  display: block;
  width: 100%;
  height: 100%;
}

.products h3 {
  display: block;
  margin: 0 80px 0 40px;
  padding-top: 50px;
  font-family: "Roboto Condensed", "Verdana", sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 30px;
  color: #ffffff;
}
@media (max-width: 1023px) {
  .products h3 {
    margin-left: 45px;
    padding-top: 40px;
  }
}
@media (max-width: 767px) {
  .products h3 {
    font-size: 18px;
    margin: 0 30px;
    padding-top: 30px;
  }
}

.message {
  padding: 170px 0 250px;
  background-color: #00b3ff;
  background-image: linear-gradient(138deg, rgba(4, 24, 39, 0.5) 33%, transparent 33%);
}
@media (max-width: 1023px) {
  .message {
    background-image: none;
    padding: 50px 0 125px;
  }
}
@media (max-width: 767px) {
  .message {
    padding-bottom: 90px;
  }
}

.message h2 {
  margin-top: 0;
  margin-bottom: 15px;
  font-family: "Roboto Condensed", "Verdana", sans-serif;
  font-size: 36px;
  line-height: 51px;
  font-weight: 400;
  color: #ffffff;
}
@media (max-width: 1023px) {
  .message h2 {
    margin-bottom: 10px;
  }
}
@media (max-width: 767px) {
  .message h2 {
    margin-bottom: 20px;
    font-size: 30px;
    line-height: 42px;
  }
}

.message p {
  margin: 0;
  margin-bottom: 35px;
  color: #ffffff;
  line-height: 22px;
}
@media (max-width: 1023px) {
  .message p {
    margin-bottom: 25px;
  }
}
@media (max-width: 767px) {
  .message p {
    margin-bottom: 40px;
    font-size: 14px;
    line-height: 19px;
  }
}

.message__form {
  position: relative;
  width: 50%;
  margin-left: auto;
}
@media (max-width: 1023px) {
  .message__form {
    width: 100%;
    margin: 0;
  }
}
.message__form::before {
  content: "";
  position: absolute;
  width: 510px;
  height: 285px;
  top: 80px;
  left: calc(-50% - 285px);
  background-image: url("../img/message.png");
}
@media (max-width: 1023px) {
  .message__form::before {
    display: none;
  }
}

.message__form fieldset {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;
  margin: 0;
  padding: 0;
  border: 0;
}
@media (max-width: 767px) {
  .message__form fieldset {
    display: block;
  }
}

.message__form input,
.message__form textarea {
  width: 100%;
  padding: 15px;
  margin-bottom: 20px;
  border: none;
  border-radius: 3px;
  line-height: 22px;
  background-color: rgba(4, 20, 39, 0.15);
  color: #ffffff;
}

.message__form textarea {
  padding-top: 25px;
  margin-bottom: 35px;
}
@media (max-width: 767px) {
  .message__form textarea {
    margin-bottom: 30px;
  }
}

.message__form button {
  display: block;
  width: 265px;
}
@media (max-width: 767px) {
  .message__form button {
    width: 100%;
    margin-bottom: 25px;
  }
}

.message__form [type=checkbox] + label {
  position: relative;
  padding-left: 25px;
  line-height: 16px;
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
}
.message__form [type=checkbox] + label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 45%;
  transform: translateY(-50%);
  width: 15px;
  height: 15px;
  border: 1px solid rgba(255, 255, 255, 0.7);
  border-radius: 2px;
}

.message__form [type=checkbox]:checked + label::before {
  border-color: #ffffff;
  background-image: url("data:image/svg+xml,%3Csvg width='9' height='6' viewBox='0 0 9 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.988525 2.33333L3.7778 5L7.96171 1' stroke='white'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-position: center;
}

.footer {
  background-color: #041827;
  color: rgba(255, 255, 255, 0.7);
}

@media (max-width: 767px) {
  footer [data-id=footer] .footer__nav-toggle,
[data-js-status=is-active] .footer__nav-toggle {
    display: block;
  }
}

.footer__nav-toggle {
  display: none;
  position: absolute;
  right: 0;
  top: 18px;
  width: 15px;
  height: 15px;
  background-color: rgba(255, 255, 255, 0);
  border: 0;
  cursor: pointer;
}

button [data-id=nav-toggle]:before,
[data-state=is-closed]:before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0;
  width: 15px;
  height: 15px;
  background: url("/img/svg/icon-plus.svg");
}

@media (max-width: 767px) {
  button [data-id=nav-toggle],
[data-state=is-closed] ~ div {
    display: none;
  }
}

button [data-id=nav-toggle]:before,
[data-state=is-opened]:before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0;
  width: 15px;
  height: 15px;
  background: url("/img/svg/icon-minus.svg");
}

.footer__container {
  display: flex;
  padding-top: 50px;
  padding-bottom: 55px;
}
@media (max-width: 1023px) {
  .footer__container {
    display: grid;
    grid-template-columns: auto 320px;
    padding-bottom: 65px;
  }
}
@media (max-width: 767px) {
  .footer__container {
    display: block;
    padding-top: 35px;
    padding-bottom: 35px;
  }
}

.footer__container > div {
  position: relative;
}
@media (max-width: 767px) {
  .footer__container > div::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: -30px;
    width: calc(100% + 54px);
    height: 1px;
    background-color: rgba(255, 255, 255, 0.7);
  }
}
@media (max-width: 767px) {
  .footer__container > div:not(:first-child) {
    padding: 20px 0;
  }
}

.footer__heading {
  margin-right: auto;
  flex-shrink: 0;
}
@media (max-width: 1023px) {
  .footer__heading {
    grid-column: 1/-1;
    display: flex;
    justify-content: space-between;
    margin-right: 0;
    margin-bottom: 35px;
  }
}
@media (max-width: 767px) {
  .footer__heading {
    flex-direction: column;
    align-items: center;
    margin-bottom: 0;
    padding-bottom: 25px;
  }
}

.footer__logo {
  display: inline-block;
  margin-bottom: 30px;
  overflow: hidden;
}
@media (max-width: 1023px) {
  .footer__logo {
    display: block;
    margin-bottom: 0;
    margin-right: auto;
  }
}
@media (max-width: 767px) {
  .footer__logo {
    margin-bottom: 15px;
    margin-right: 0;
  }
}

.footer__title {
  margin: 0;
  margin-bottom: 20px;
  font-family: "Roboto Condensed", "Verdana", sans-serif;
  color: #ffffff;
  font-size: 15px;
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}
@media (max-width: 767px) {
  .footer__title {
    margin-bottom: 0;
  }
}

.footer__nav-wrapper {
  display: grid;
  grid-template-columns: 220px 220px;
  column-gap: 30px;
}
@media (max-width: 1023px) {
  .footer__nav-wrapper {
    grid-template-columns: auto;
  }
}

.footer__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 767px) {
  .footer__list {
    margin-top: 20px;
  }
}
@media (max-width: 767px) {
  .footer__list--sub {
    margin-top: 0;
  }
}

.footer__list-item {
  padding-bottom: 10px;
  word-wrap: break-word;
}

.footer__nav-link {
  color: rgba(255, 255, 255, 0.7);
  line-height: 22px;
}

.footer__address {
  margin-bottom: 10px;
  font-style: normal;
}

.footer__contacts-link {
  color: rgba(255, 255, 255, 0.7);
  line-height: 25px;
}

.social__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.social__item {
  margin-right: 15px;
}
.social__item:last-child {
  margin-right: 0;
}

.social__link {
  display: flex;
  align-items: center;
  padding: 5px;
}

